interface RouteConfig {
    pattern: RegExp;
    target: (matches: RegExpMatchArray) => string;
    headers: Record<string, string>;
    bodyMapper?: (original: unknown) => unknown;
}

type ResponseAdapter<T = unknown> = (raw: any) => T;

const sendRequest = async (url: string, options: any) => {
    // ä¿®æ”¹è¯·æ±‚çš„ headers
    if (options && options.headers) {
        if (options.headers.Authorization) {
            delete options.headers.Authorization;
        } else if (options.headers.authorization) {
            delete options.headers.authorization;
        }
    }

    const isFormData = options.body instanceof FormData;

    const headers = {
        ...options.headers,
        'authorization': `Bearer ${BLOCK_BRAIN_TOKEN}`,
        ...(!isFormData && {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        })
    };

    // åˆ›å»ºæ–°çš„è¯·æ±‚é€‰é¡¹
    const newOptions: RequestInit = {
        ...options,
        headers,
    };

    if (newOptions.body && typeof newOptions.body !== 'string' && !isFormData) {
        newOptions.body = JSON.stringify(newOptions.body);
    }

    let retries = 0;
    const maxRetries = 3;

    while (retries < maxRetries) {
        try {
            const response = await fetch(url, newOptions);

            // æœªæŽˆæƒé”™è¯¯ä¸é‡è¯•
            if (response.status === 401) {
                const error = await response.json();
                throw error;
            }

            if (!response.ok) {
                const error = await response.json();
                console.log(JSON.stringify({ type: 'Response Error', error }, null, 2));
                retries++;
                if (retries === maxRetries) {
                    throw error;
                }
                continue;
            }

            const responseClone = response.clone();
            const jsonResponse = await responseClone.json();
            console.log(JSON.stringify({ type: 'Response----------------------------------', response: jsonResponse }, null, 2));

            return response;

        } catch (error) {
            if (error.status === 401) {
                throw error;
            }
            retries++;
            if (retries === maxRetries) {
                throw error;
            }
        }
    }
};

