// blockbrainçš„æ¶ˆæ¯ç»“æž„
interface BlockBrainMessage {
    _id: string;
    createdAt: string;
    role: 'user' | 'assistant';
    content: string;
    model: string;
}

// æœ¬åœ°æ¶ˆæ¯ç»“æž„
interface LocalMessage {
    id: string;
    parentId: string | null;
    childrenIds: string[];
    role: string;
    content: string;
    timestamp: number;
    models: string[];
    // å…¶ä»–å¯é€‰å­—æ®µ
    model?: string;
    modelName?: string;
    modelIdx?: number;
    userContext?: null;
    usage?: object;
    done?: boolean;
}

// ä»Žblockbrainçš„æ¶ˆæ¯åˆ—è¡¨è½¬æ¢ä¸ºæœ¬åœ°æ¶ˆæ¯åˆ—è¡¨
function convertMessageArrayFromBlockBrainToLocal(messages: BlockBrainMessage[]): { messages: LocalMessage[] } {
    // æŒ‰æ—¶é—´å‡åºæŽ’åº
    const sorted = [...messages].sort((a, b) =>
        new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
    );

    const intermediate: LocalMessage[] = [];
    let previousId: string | null = null;

    sorted.forEach(current => {
        const timestamp = Math.floor(new Date(current.createdAt).getTime() / 1000);
        const curModel = current.model ?? '';

        const message: LocalMessage = {
            id: current._id,
            parentId: previousId,
            childrenIds: [],
            role: current.role,
            content: current.content,
            timestamp,
            models: [curModel],
            // æ·»åŠ è¾…åŠ©å­—æ®µ
            model: curModel,
            modelName: curModel,
            modelIdx: 0
        };

        if (previousId) {
            const prevMsg = intermediate.find(m => m.id === previousId);
            if (prevMsg) prevMsg.childrenIds.push(current._id);
        }

        intermediate.push(message);
        previousId = current._id;
    });

    return { messages: intermediate };
}

// ä»Žæœ¬åœ°æ¶ˆæ¯æ•°ç»„è½¬æ¢ä¸ºä»¥idä¸ºkeyçš„map
function convertLocalMessagesToMap(intermediate: { messages: IntermediateMessage[] }): Record<string, IntermediateMessage> {
    return intermediate.messages.reduce((acc, cur) => {
        acc[cur.id] = cur;
        return acc;
    }, {} as Record<string, IntermediateMessage>);
}

