export const apiFetch = async (url: string, options: RequestInit = {}) => {
    if (requestConfigMap[url]) {
        // å¦‚æžœè¯·æ±‚çš„ URL åœ¨ requestConfigMap ä¸­ï¼Œç›´æŽ¥è°ƒç”¨å¯¹åº”çš„ reactFunc
        const response = await requestConfigMap[url].reactFunc(options);
        return response;
    }

    const matchRule = API_PATTERN_MAP.find(rule =>
        rule.pattern.test(url)
        && (rule.method !== null && rule.method === options.method));
    if (matchRule) {
        const matches = url.match(matchRule.pattern);
        if (matches) {
            return matchRule.reactFunc(matches, options);
        }
    }

    // ä¿®æ”¹è¯·æ±‚çš„ URL
    //   const newUrl = `${API_BASE_URL}${url}`;
    const newUrl = url;

    // ä¿®æ”¹è¯·æ±‚çš„ headers
    const headers = {
        ...options.headers,
        // 'Authorization': `Bearer ${NEW_TOKEN}`,
        // 'Content-Type': 'application/json',
    };

    // åˆ›å»ºæ–°çš„è¯·æ±‚é€‰é¡¹
    const newOptions: RequestInit = {
        ...options,
        headers,
    };

    if (newOptions.body) {
        const bodyJsonObject = JSON.parse(newOptions.body);
        if (bodyJsonObject) {
            bodyJsonObject.model = 'deepseek-r1:latest';
            newOptions.body = JSON.stringify(bodyJsonObject);
        }
    }

    // å‘å‡ºè¯·æ±‚
    console.log(JSON.stringify({ type: 'Request=====================================', url: newUrl, options: newOptions }, null, 2));

    const response = await fetch(newUrl, newOptions);

    // æ£€æŸ¥å“åº”çŠ¶æ€
    if (!response.ok) {
        const error = await response.json();
        console.log(JSON.stringify({ type: 'Response Error', error }, null, 2));
        throw error;
    }

    const responseClone = response.clone();
    const jsonResponse = await responseClone.json();
    console.log(JSON.stringify({ type: 'Response----------------------------------', response: jsonResponse }, null, 2));

    return response;
};